import { useEffect, useState } from "react";
import { Nav, Navbar, Container } from 'react-bootstrap';
import logotipo from '@assets/logos/Logotipo-Branco-SemFundo.png';
import Scrollspy from 'react-scrollspy';
import './styles.css';

function Topbar() {

	const [style, setStyle] = useState(false);

	useEffect(() => {
		window.addEventListener("scroll", () => {
			if (window.pageYOffset > 60) {
				setStyle(true);
			} else {
				setStyle(false);
			}
		});
	});

	return (
		<Navbar bg="dark" variant='dark' expand="lg" fixed="top" className={style ? 'colorChange' : ''}>
			<Container >
				<Navbar.Brand href="/" title="inicio secomp beta">
					<h1 style={{margin:0}}>
						<img src={logotipo} alt="Secomp BETA UFSCar São Carlos" border="0" width={200} />
					</h1>
				</Navbar.Brand>
				<Navbar.Toggle aria-controls="basic-navbar-nav" />
				<Navbar.Collapse id="basic-navbar-nav">
					<Scrollspy className="me-auto navbar" items={['sobre', 'cronograma', 'apoio', 'equipe']} componentTag={'div'} currentClassName="is-current">
						<Nav.Link className='header' href="#sobre">
							<div className='header-container'>
								<p className='header-link'>Sobre</p>
							</div>
						</Nav.Link>
						<Nav.Link className='header' href="#cronograma">
							<div className='header-container'>
								<p className='header-link'>Cronograma</p>
							</div>
						</Nav.Link>
						<Nav.Link className='header' href="#apoio">
							<div className='header-container'>
								<p className='header-link'>Apoio</p>
							</div>
						</Nav.Link>
						<Nav.Link className='header' href="#equipe">
							<div className='header-container'>
								<p className='header-link'>Equipe</p>
							</div>
						</Nav.Link>
					</Scrollspy>
					<Nav.Link className='header' href="https://google.com">
						<button>Inscrever-se</button>
					</Nav.Link>
				</Navbar.Collapse>
			</Container>
		</Navbar>
	);
}

export default Topbar;