import { listaEquipe } from '@utils/listaEquipe';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
    /*faFacebook,
    faTwitter,
    faInstagram, */
    faLinkedin,
    faGithub
} from "@fortawesome/free-brands-svg-icons";
import './styles.css';

function Equipe() {

    const lista = listaEquipe();

    return (
        <div id="equipe" className="container">
            <h3>Equipe</h3><hr size="10" width="100%" />
            <div className="row">
                {lista.map(pessoa =>
                    <div className="col-sm-12 col-lg-6 col-xl-4 mb-3" key={pessoa.nome}>
                        <div className='card pessoa'>
                            <div><img src={pessoa.img} alt="foto integrante" width={140} /></div>
                            
                            <div className="box">
                                <h4>{pessoa.nome}</h4>
                                <i>{pessoa.diretoria}</i>
                                <div className='redes-sociais'>
                                    <a href={pessoa.linkedin} className="linkedin social">
                                        <FontAwesomeIcon icon={faLinkedin} size="2x" />
                                    </a>
                                    <a href={pessoa.github} className="github social">
                                        <FontAwesomeIcon icon={faGithub} size="2x" />
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
}

export default Equipe;