import React, { useEffect, useState } from "react";
import Parallax from "@components/Parallax";
import Sobre from "@components/Sobre";
import Cronograma from "@components/Cronograma";
import Apoio from "@components/Apoio";
import Equipe from "@components/Equipe";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowCircleUp } from "@fortawesome/fontawesome-free-solid";
import './styles.css';

function Home() {

	const [showButton, setShowButton] = useState(false);

	/* https://www.kindacode.com/article/how-to-create-a-scroll-to-top-button-in-react/ */
	useEffect(() => {
		window.addEventListener("scroll", () => {
			if (window.pageYOffset > 300) {
				setShowButton(true);
			} else {
				setShowButton(false);
			}
		});
	}, []);

	const scrollToTop = () => {
		window.scrollTo({
			top: 0,
			behavior: 'smooth'
		});
	};
	/***/

	return (
		<div id="home">
			<Parallax />
			<Sobre />
			<Cronograma />
			<Apoio />
			<Equipe />
			{showButton && (
				<FontAwesomeIcon className="back-to-top" onClick={scrollToTop} icon={faArrowCircleUp} size="1x" />
			)}
		</div>
	);
}

export default Home;